* {
  box-sizing: border-box; }

html {
  color: #111111;
  font-size: 15px;
  font-family: Arial, Helvetica, sans-serif; }

body,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure {
  margin: 0;
  padding: 0; }

ul,
ol {
  list-style: none; }

a {
  text-decoration: none; }

input,
button {
  font-size: 1rem; }

.text-bold {
  font-weight: 800; }

.text-regular {
  font-weight: 500; }

.page-main {
  display: flex;
  justify-content: center; }

.no-padding {
  padding: 0 !important; }

.input-field {
  min-width: 300px;
  box-sizing: border-box;
  display: inline-block;
  padding-bottom: 20px;
  position: relative; }
  .input-field > * {
    display: block; }
  .input-field__input {
    background: #efefef;
    border: none;
    width: 100%;
    border-radius: 5px;
    padding: 10px 15px; }
  .input-field__error {
    position: absolute;
    bottom: 0; }

.sign-up-form {
  max-width: 450px;
  margin: 0 auto; }
  .sign-up-form__input {
    width: 100%; }
  .sign-up-form__button {
    width: 100%; }

.sign-in-form {
  max-width: 450px;
  margin: 0 auto; }
  .sign-in-form__input {
    width: 100%; }
  .sign-in-form__button {
    width: 100%;
    background: khaki;
    border-radius: 9px; }
